.NavThree {
    height: 25px;
    display: flex;
    justify-content: space-evenly;
}

.selected-nav {
    padding: 5px 10px;
    color: #396bf6;
    /* border: 1px solid rgb(194, 194, 194); */
    /* border-radius: 5px; */
}

.unselected {
    padding: 5px 10px;
    color: black;
    transition: color 0.3s ease;
}

.unselected:hover {
    color: #396bf6;
}

.active {
    color: green;
}

@media (max-width: 666px) {
    .NavThree {
        width: 70%;
        position: relative;
        top: -305px;
        right: -15%;
    }
}

@media (max-width: 545px) {
    .NavThree {
        width: 52%;
        font-size: 11px;
        position: relative;
        top: -305px;
        right: -24%;
    }
}